<template>
<div class="order-wrapper">
  <h1>Services</h1>
  <div class="services">
      <span
      class="service"
      v-for="(service, index) in services"
      :key="index"
      :class="{active: service.active}"
      @click="active(index)">
        <span>
             {{service.name}}</span> <span>${{service.price}}
        </span>
      </span>
  </div>
  <div class="total-wrapper">
      <span>Total:</span> <span>${{total}}</span>
  </div>
</div>
</template>

<script>
export default {
    data(){
        return{
            total: 0,
            services: [
                {name: "Web Development", price: 300.00},
                {name: "Design", price: 400.00},
                {name: "Intergration", price: 250.00},
                {name: "Training", price: 220.00}
            ]
        }
    },
    methods:{
        active(index) {
            let service = this.services[index];
            service.active = !service.active;
            if(service.active == true){
            this.total += this.services[index].price;
            }
            if(service.active == false){
            this.total -= this.services[index].price;
            }

        }
    }
}
</script>

<style>
    .order-wrapper{
        width: 50%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        background: #61a1bc;
        margin: auto;
        margin-top: 50px;
    }
    h1{
        text-align: center;
        color: #fff;
        font-family: 'Cookie', cursive;
        text-shadow: 0 3px 0 rgba(0,0,0,0.1);
    }
    .services{
        display: flex;
        flex-direction: column;
        width: 100%;
    }
    .service{
        display: flex;
        justify-content: space-between;
        color: #fff;
        font-size: 20px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: 600;
        padding: 20px 25px;
        background: #e35885;
        margin: 0 30px;
        margin-bottom: 15px;
        cursor: pointer;
    }
    .service.active{
        background: #8ec16d;
    }
    .total-wrapper{
        display: flex;
        justify-content: space-between;
        border-top: 1px solid #fff;
        margin: 0 30px;
        margin-bottom: 15px;
        padding-top: 10px;
    }
    .total-wrapper span{
        color: #fff;
        font-size: 20px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        font-weight: 600;
    }
    @media screen and (max-width: 992px) {
    .order-wrapper{
        width: 100%;
    }
}
</style>