<template>
<h2>Search for Marvel Movies</h2>
<form>
    <input type="text" placeholder="Search..." v-model="searchFilter">
</form>
<div class="items-wrapper">
    <div v-for="item in searchMovie" :key="item" class="item-box">
        <img :src="item.image" alt="">
        <span class="item-information-holder">
            <span class="title">{{item.title}}</span>
            <span class="description">{{item.description}}</span>
        </span>
    </div>
</div>
</template>

<script>
export default {
    data(){
        return{
            searchFilter:"",
            items:
            [
                {
                image:"https://ucarecdn.com/f3bb3571-68bc-47e1-a980-cf8e7ccc2bcb/-/preview/", 
                title: "Spider-man", 
                description:"Under the watchful eye of mentor Tony Stark, high school student Peter Parker starts to embrace his newfound identity as Spider-Man."
                },
                {
                image:"https://www.searchpng.com/wp-content/uploads/2019/03/Captain-America-PNG-image-1024x1024.png", 
                title: "Captain America: The First Avenger", 
                description:"After an experimental program turns a man into a supersoldier, he, now known as Captain America, leads the fight against Red Skull..."
                },
                {
                image:"https://thedirect.s3.amazonaws.com/media/article_full/tonystarkposterthumbnail_sc4Eg2D_MvjiP53_KEqdaPZ_S3uRid2.jpg", 
                title: "Iron Man", 
                description:"Tony Stark, a playboy and engineering genius, is captured whilst in Afghanistan demonstrating a new missile."
                },
                {
                image:"https://static1.srcdn.com/wordpress/wp-content/uploads/2019/01/Thor-and-Stormbreaker.jpg?q=50&fit=crop&w=960&h=500&dpr=1.5", 
                title: "Thor", 
                description:"After his reckless actions reignite an ancient war, the Norse god is stripped of his powers, cast out of Asgard and forced to live among hum..."
                },
                {
                image:"https://wallup.net/wp-content/uploads/2017/11/17/375654-Black_Widow-Avengers_Age_of_Ultron.jpg", 
                title: "Black Widow", 
                description:"In Marvel Studios’ action-packed spy thriller “Black Widow,” Natasha Romanoff aka Black Widow confronts the darker parts of her ledger when a dangerous conspiracy with ties to her past arises."
                },
            ]
        }
    },
    computed:{
        searchMovie(){
            let movie = this.items;
            if(this.searchFilter != ""){
                movie = movie.filter((item) => {
                    return item.title.includes(this.searchFilter);
                })
            }
            return movie;
        }
    }
}

</script>

<style scoped>
input{
    border: 2px solid #e9e9e9;
    padding: 5px 10px;
    font-size: 20px;
    margin-bottom: 30px;
}
input:placeholder{
color: #e9e9e9;
}
input:focus{
    outline: none;
}
h2{
    text-align: center;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 30px;
}
form{
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
.items-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    widows: 100%;
}
.item-box{
    width: 50%;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    justify-content: space-between;
}
.item-box img{
    width: 35%;
    object-fit: cover;
